<template>
   <section class="section">
       <div class="mine">
           <div class="mine-top"></div>
           <div class="mine-main">
                <div class="user c333">
                    <p v-show="show">{{user}}
                        <span @click="cancel"></span>
                    </p>
                    <div v-show="!show">
                        <div class="mine-register unlogin" @click="jump('register')">
                            
                        </div>
                        <div class="mine-login unlogin" @click="jump('login')">
                            
                        </div>
                    </div>
                </div>
                <div class="mainbox">
                    <p>
                        <i class="iconfont icon-xiaoxi c2dbae8"></i>
                        <span class="c5c5c"></span>
                        <i class="iconfont icon-enter"></i>
                        </p>
                </div>
               <div class="mainbox">
                   <p>
                       <i class="iconfont icon-liulanjilu cf277"></i>
                       <span class="c5c5c"></span>
                       <i class="iconfont icon-enter"></i>
                    </p>
                    <p>
                       <i class="iconfont icon-hetongchaxun c72c0"></i>
                       <span class="c5c5c"></span>
                       <i class="iconfont icon-enter"></i>
                    </p>
               </div>
               <div class="mainbox">
                   <p>
                       <i class="iconfont icon-tuoguan c19b2"></i>
                       <span class="c5c5c"></span>
                       <i class="iconfont icon-enter"></i>
                    </p>
                    <p>
                       <i class="iconfont icon-fangdaijisuanqi cf277"></i>
                       <span class="c5c5c"></span>
                       <i class="iconfont icon-enter"></i>
                    </p>
               </div>
               <div class="mainbox">
                   <p>
                       <i class="iconfont icon-ttpodicon cecc63f"></i>
                       <span class="c5c5c"></span>
                       <i class="iconfont icon-enter"></i>
                    </p>
               </div>
           </div>
       </div>
   </section>
</template>

<script>
import router from "../views/router"
import {mapState,mapActions} from "vuex";
export default {
    data(){
        return {
            show:true,
            user:""
        }
    },
    computed:mapState({
        // ifshow:"show",
        // user:"username"
    }),
    methods:{
        ...mapActions([
            "jump",
            "goback"
            ]  
        ),
        cancel(){
            localStorage.clear();
            router.push("/index/mine");
        },
    },
    mounted(){
        if(localStorage.username){
            this.user = localStorage.username;
        }else{
            this.show = false;
        }
    },
}
</script>

<style scoped>
    
</style>



