<template>
  <section class="section">
    <header>
        <div>
            <a @click="goback" class="return"><img :src="src"></a>
            <a @click="jump('password')" class="password"></a>
            <h1>{{title}}</h1>
        </div>
    </header>
    <div class="login-banner">
        <img :src="loginbanner">
    </div>
    <main>
        <div class="text-wrap">
            <p></p>
        </div>
        <div class="mt25">
            <div class="text-wrap">
                <p>
                    <span></span><input type="text" placeholder="" v-model="phone">
                </p>  
            </div>
            <div class="text-wrap">
                <p>
                    <span></span><input type="password" placeholder="" v-model="password">
                </p>
                
            </div>
        </div>
        <div class="login-btn-box">
            <button @click="jump('register')"></button>
            <button @click="checklogin"></button>
        </div>
    </main>
  </section>
</template>

<script>
import {mapActions} from "vuex";
import { MessageBox } from 'mint-ui';
import p1 from "../../assets/images/jt.png"
import p2 from "../../assets/images/login.png"
import router from "./router"
export default {
    data(){
        return {
            src:p1,
            loginbanner:p2,
            title:"",
            phone:"",
            password:""
        }
    },
    methods:{
        checklogin(){
            var that = this;
            $.ajax({
                url:"http://119.23.238.121:7700/user/login",
                type:"post",
                data:{
                    username:this.phone,
                    password:this.password
                },
                success(data){
                    if(data=="!"){
                        localStorage.username=that.phone;
                        router.push("/index/home");
                    }else{
                        MessageBox('', '');
                    }
                }
            });
        },
        ...mapActions([
            "jump",
            "goback"
            ]  
        )
    },
}
</script>
<style scoped>
    .MessageBox{
        font-size: 45px;
    }
</style>





